document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	let Editable = e.detail;
	let Abstract = Editable.editables._abstract;

	class SubscriptionPeriod extends Abstract {

		render() {
			let interval_select = this.getElement().querySelector( 'select[name="interval"]' );
			if ( interval_select ) {
				this.popuplateSelect( interval_select, this._args.intervals );
			}

			let period_select = this.getElement().querySelector( 'select[name="period"]' );
			if ( period_select ) {
				this.popuplateSelect( period_select, this._args.periods );
			}

		}

		valueToInput( value ) {
			if ( !value ) {
				return;
			}

			let interval_select = this.getElement().querySelector( 'select[name="interval"]' );
			if ( interval_select && value.hasOwnProperty( 'interval' ) ) {
				interval_select.value = value.interval;
			}

			let period_select = this.getElement().querySelector( 'select[name="period"]' );
			if ( period_select && value.hasOwnProperty( 'interval' ) ) {
				period_select.value = value.period;
			}
		}

		popuplateSelect( select, options ) {
			Object.keys( options ).forEach( ( k ) => {
				let el = document.createElement( 'option' );

				el.setAttribute( 'value', k );
				el.innerText = options[ k ];

				select.append( el );
			} );
		}

		getValue() {
			return {
				interval : this.getElement().querySelector( '[name=interval]' ).value,
				period : this.getElement().querySelector( '[name=period]' ).value,
			};
		}

		getTemplate() {
			return `	
				<div class="input__group">
					<select name="interval"  class="select__medium"></select>&nbsp;
					<select name="period"  class="select__medium"></select>
				</div>
			`;
		}

		_setDefaults() {
			this._defaults.type = 'wc_subscription_period';
		}
	}

	Editable.registerEditable( 'wc_subscription_period', SubscriptionPeriod );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( '_abstract' );

	class SubscriptionPeriod extends Abstract {
		getEditable() {
			let editable = MiddleWare.Editables.get( 'wc_subscription_period' );

			return editable ? editable : false;
		}

		map() {
			this.args.periods = this.column.editable.period_options;
			this.args.intervals = this.column.editable.interval_options;

			return this.args;
		}

	}

	MiddleWare.register( 'wc_subscription_period', SubscriptionPeriod );
} );