document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	const i18n = {
		LIMIT : acp_woocommerce_i18n.woocommerce.usage_limit_per_coupon,
		LIMITPERUSER : acp_woocommerce_i18n.woocommerce.usage_limit_per_user,
		LIMITPRODUCT : acp_woocommerce_i18n.woocommerce.usage_limit_products,
	};
	let Editable = e.detail;
	let Abstract = Editable.editables._abstract;

	class Usage extends Abstract {

		valueToInput( value ) {
			this.getElement().querySelector( '[name=limit]' ).focus();

			if ( !value ) {
				return;
			}

			this.getElement().querySelector( '[name=limit]' ).value = value.usage_limit;
			this.getElement().querySelector( '[name=user_limit]' ).value = value.usage_limit_per_user;
			this.getElement().querySelector( '[name=product_limit]' ).value = value.usage_limit_products;

		}

		getValue() {
			return {
				usage_limit : this.getElement().querySelector( '[name=limit]' ).value,
				usage_limit_per_user : this.getElement().querySelector( '[name=user_limit]' ).value,
				usage_limit_products : this.getElement().querySelector( '[name=product_limit]' ).value
			};
		}

		getTemplate() {
			const limit = this._template.form.input( 'limit', null, { type : 'number' } ).outerHTML;
			const use_limit = this._template.form.input( 'user_limit', null, { type : 'number' } ).outerHTML;
			const use_product = this._template.form.input( 'product_limit', null, { type : 'number' } ).outerHTML;

			return `	
				<div class="input__group">
					<label>${i18n.LIMIT}</label>
					<div class="input__controlgroup">${limit}</div>
				</div>
				<div class="input__group">
					<label>${i18n.LIMITPERUSER}</label>
					<div class="input__controlgroup">${use_limit}</div>
				</div>
				<div class="input__group">
					<label>${i18n.LIMITPRODUCT}</label>
					<div class="input__controlgroup">${use_product}</div>
				</div>	
			`;
		}

		_setDefaults() {
			this._defaults.type = 'wc_usage';
		}
	}

	Editable.registerEditable( 'wc_usage', Usage );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( '_abstract' );

	class Usage extends Abstract {
		getEditable() {
			let editable = MiddleWare.Editables.get( 'wc_usage' );

			return editable ? editable : false;
		}
	}

	MiddleWare.register( 'wc_usage', Usage );
} );