document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	const i18N = {
		WIDTH : acp_woocommerce_i18n.woocommerce.width,
		LENGTH : acp_woocommerce_i18n.woocommerce.length,
		HEIGHT : acp_woocommerce_i18n.woocommerce.height,
	};
	let Editable = e.detail;
	let Abstract = Editable.editables._abstract;

	class Dimensions extends Abstract {

		valueToInput( value ) {
			this.getElement().querySelector( '[name=length]' ).focus();

			if ( !value ) {
				return;
			}

			this.getElement().querySelector( '[name=length]' ).value = value.length;
			this.getElement().querySelector( '[name=width]' ).value = value.width;
			this.getElement().querySelector( '[name=height]' ).value = value.height;
		}

		getValue() {
			return {
				length : this.getElement().querySelector( '[name=length]' ).value,
				width : this.getElement().querySelector( '[name=width]' ).value,
				height : this.getElement().querySelector( '[name=height]' ).value
			};
		}

		getTemplate() {
			const length = this._template.form.input( 'length', null, { type : 'number', step : 'any', placeholder : i18N.LENGTH } ).outerHTML;
			const width = this._template.form.input( 'width', null, { type : 'number', step : 'any', placeholder : i18N.WIDTH } ).outerHTML;
			const height = this._template.form.input( 'height', null, { type : 'number', step : 'any', placeholder : i18N.HEIGHT } ).outerHTML;

			return `<div class="aceditable__form__inputs__dimensions">${length} ${width} ${height}</div>`;
		}

	}

	Editable.registerEditable( 'dimensions', Dimensions );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( '_abstract' );

	class Dimensions extends Abstract {
		getEditable() {
			let editable = MiddleWare.Editables.get( 'dimensions' );

			return editable ? editable : false;
		}
	}

	MiddleWare.register( 'dimensions', Dimensions );
} );