'use strict';

document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	const i18n = {
		REGULAR : acp_woocommerce_i18n.woocommerce.regular,
		SALE : acp_woocommerce_i18n.woocommerce.sale,
		FROM : acp_woocommerce_i18n.woocommerce.sale_from,
		UNTIL : acp_woocommerce_i18n.woocommerce.sale_to,
		SCHEDULE : acp_woocommerce_i18n.woocommerce.schedule,
	};
	const Editable = e.detail;
	const Abstract = Editable.editables._abstract;

	class Price extends Abstract {

		render() {
			this.toggleSchedule( false );
			this.attachDatePicker();

			if ( woocommerce_admin_meta_boxes && woocommerce_admin_meta_boxes.hasOwnProperty( 'currency_format_symbol' ) ) {
				this.getElement().querySelectorAll( '[data-symbol]' ).forEach( ( input ) => {
					input.innerHTML = woocommerce_admin_meta_boxes.currency_format_symbol;
				} );
			}


		}

		valueToInput( value ) {
			this.getElement().querySelector( '[name=regular]' ).focus();

			if ( value ) {
				this.getElement().querySelector( '[name=regular]' ).value = value.regular_price;
				this.getElement().querySelector( '[name=sale]' ).value = value.sale_price;
				this.getElement().querySelector( '[name=date_from]' ).value = value.sale_price_dates_from;
				this.getElement().querySelector( '[name=date_to]' ).value = value.sale_price_dates_to;
			}

			this.addScheduleEvents();
		}

		attachDatePicker() {
			document.body.classList.add( 'ac-jqui' );
			let dates = jQuery( this.getElement() ).find( '[name=date_from], [name=date_to]' );
			dates.datepicker( {
				dateFormat : 'yy-mm-dd',
				changeYear : true,
				onSelect : function( selectedDate ) {
					let option = jQuery( this ).is( '[name="date_from"]' ) ? 'minDate' : 'maxDate';
					let instance = jQuery( this ).data( 'datepicker' );
					let date = jQuery.datepicker.parseDate(
						instance.settings.dateFormat || jQuery.datepicker._defaults.dateFormat,
						selectedDate,
						instance.settings
					);

					dates.not( jQuery( this ) ).datepicker( 'option', option, date );
				}
			} );
		}

		addScheduleEvents() {
			if (
				this.getElement().querySelector( '[name=date_from]' ).value ||
				this.getElement().querySelector( '[name=date_to]' ).value
			) {
				this.toggleSchedule( true );
				this.getElement().querySelector( '[name=schedule]' ).checked = true;
			}

			this.getElement().querySelector( '[name=schedule]' ).addEventListener( 'change', ( e ) => {
				let target = e.target;

				this.toggleSchedule( target.checked );
			} );
		}

		toggleSchedule( show = true ) {
			let element = this.getElement().querySelector( '[data-toggle-schedule]' );

			if ( show ) {
				element.style.display = 'block';
			} else {
				element.style.display = 'none';

				this.getElement().querySelector( '[name=date_from]' ).value = '';
				this.getElement().querySelector( '[name=date_to]' ).value = '';
			}
		}

		getValue() {
			return {
				regular_price : this.getElement().querySelector( '[name=regular]' ).value,
				sale_price : this.getElement().querySelector( '[name=sale]' ).value,
				sale_price_dates_from : this.getElement().querySelector( '[name=date_from]' ).value,
				sale_price_dates_to : this.getElement().querySelector( '[name=date_to]' ).value
			};
		}

		getTemplate() {
			const regular = this._template.form.input( 'regular', null ).outerHTML;
			const sale = this._template.form.input( 'sale', null ).outerHTML;
			const from = this._template.form.input( 'date_from', null, { placeholder : 'yyyy-mm-dd' } ).outerHTML;
			const until = this._template.form.input( 'date_to', null, { placeholder : 'yyyy-mm-dd' } ).outerHTML;

			return `
			<div class="aceditable__form__inputs__price">
				<div class="input__group">
					<label>${i18n.REGULAR}</label>
					<div class="input__controlgroup">
						<span class="input__control__prepend" data-symbol="">$</span>
						${regular}
					</div>
				</div>
				<div class="input__group">
					<label>${i18n.SALE}</label>
					<div class="input__controlgroup">
						<span class="input__control__prepend" data-symbol="">$</span>
						${sale}
					</div>
				</div>
				<div class="input__group">
					<label><input type="checkbox" name="schedule"> ${i18n.SCHEDULE}</label>
				</div>
				<div class="" data-toggle-schedule="">
					<div class="input__group">
						<label>${i18n.FROM}</label>
						<div class="input__controlgroup">
							${from}
						</div>
					</div>
					<div class="input__group">
						<label>${i18n.UNTIL}</label>
						<div class="input__controlgroup">
							${until}
						</div>
					</div>
				</div>
				
				
			</div>
			`;
		}

	}

	Editable.registerEditable( 'wc_price', Price );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( '_abstract' );

	class Price extends Abstract {
		getEditable() {
			let editable = MiddleWare.Editables.get( 'wc_price' );

			return editable ? editable : false;
		}

		map() {
			this.args.default_type = this.column.editable.default_type;

			return this.args;
		}
	}

	MiddleWare.register( 'wc_price', Price );
} );