document.addEventListener( 'AC_Editing_Register_Editables', ( e ) => {
	let Editable = e.detail;
	let Abstract = Editable.editables._abstract;

	class Variation extends Abstract {

		valueToInput( cellvalue ) {
			let value = cellvalue.value;
			let attributes = cellvalue.options;

			if ( attributes ) {
				this.setAttributes( attributes );
			}

			Object.keys( value ).forEach( ( v ) => {
				let select = this.getElement().querySelector( `select[name=${v}]` );
				if ( select ) {
					select.value = value[ v ];
				}
			} );
		}

		getValue() {
			let result = {};
			this.getElement().querySelectorAll( 'select' ).forEach( ( select ) => {
				result[ select.dataset.attribute ] = select.value;
			} );

			return result;
		}

		setAttributes( attributes ) {
			let element = this.getElement().querySelector( '.attributes' );
			Object.keys( attributes ).forEach( ( key ) => {
				let attribute = attributes[ key ];
				element.insertAdjacentHTML( 'beforeEnd', this.getAttributeSelect( key, attribute.label, attribute.options ) );
			} );
		}

		getAttributeSelect( key, label, options ) {
			let select = document.createElement( 'select' );
			select.setAttribute( 'name', key );
			select.dataset.attribute = key;

			select.innerHTML = `<option value="">Any ${label}</option>`;

			Object.keys( options ).forEach( ( key ) => {
				select.insertAdjacentHTML( 'beforeEnd', `<option value="${key}" >${options[ key ]}</option>` )
			} );

			return this._template.form.inputGroup( label, select.outerHTML );
		}

		getTemplate() {
			return `<div class="attributes"></div>`;
		}

		_setDefaults() {
			this._defaults.type = 'wc_variation';
		}
	}

	Editable.registerEditable( 'wc_variation', Variation );
} );

// Register MiddleWare
document.addEventListener( 'AC_Editing_Register_Middleware', ( e ) => {
	const MiddleWare = e.detail;
	const Abstract = MiddleWare.middleware.get( '_abstract' );

	class Variation extends Abstract {
		getEditable() {
			let editable = MiddleWare.Editables.get( 'wc_variation' );

			return editable ? editable : false;
		}
	}

	MiddleWare.register( 'wc_variation', Variation );
} );